* {
  margin: 0;
  padding: 0;
}

body {
  font-size: 10px;
  /* background-color: #b9b9b9; */
  background-color: #181818bb;
}
.calculator {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-color: white;
  border-radius: 6px;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.11);
  transition: 0.2s;
}

.btnBlock {
  padding: 10px 30px;
}

.btn {
  position: relative;
  width: 5rem;
  height: 5rem;
  border-radius: 400px;
  padding: 8px;
  text-align: center;
  margin: 4px;
  box-shadow: 0px 3px 0.8rem rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: 0.2s;
}

@media (max-width: 770px) {
  .btn {
    width: 4rem;
    height: 4rem;
  }
}
@media (max-width: 640px) {
  .btn {
    width: 4rem;
    height: 4rem;
    margin-left: 10px;
  }

  .btn:first-child {
    margin-left: 0;
  }
  .calculator {
    width: 95%;
  }
  .btnBlock {
    width: 100%;
    padding: 10px;
  }
  .row {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.btn:hover {
  background-color: rgba(128, 128, 128, 0.774);
  transform: translateY(-2px);
  box-shadow: 0px 3px 1.2rem rgba(0, 0, 0, 0.2);
}

.btn:active {
  box-shadow: 0px 0px 0.8rem rgba(0, 0, 0, 0.2);
  transform: translateY(2px);
}

.btn:hover .value {
  color: aliceblue;
}
.value {
  position: absolute;
  width: 6.5rem;
  height: 6rem;
  top: 50%;
  border: 0;
  background-color: transparent;
  padding: 0;
  font-size: 2rem;
  left: 50%;
  user-select: none;
  transform: translateY(-50%) translateX(-50%);
}

.value:focus {
  outline: 0;
}

.display {
  width: 99%;
  height: 26vh;
  background-color: rgb(228, 228, 228);
  border-radius: 4px 4px 0 0;
  margin: 2px;
  padding: 15px;
  font-size: 3rem;
  text-align: right;
}
